* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Kanit", sans-serif !important;
  line-height: 1.3;
  color: #ebebeb;
  font-weight: normal;
}

input:focus {
  outline: none !important;
}

input::-ms-input-placeholder {
  /* Edge 12-18 */
  color: #fff !important;
}

input::placeholder,
textarea::placeholder {
  color: #ffffffb9 !important;
}

.ant-input-outlined:focus,
.ant-input-outlined:hover,
.ant-input-outlined:focus-within {
  border: 0 !important;
  box-shadow: none !important;
  background-color: transparent;
}
.ant-select-selector :focus,
.ant-select-selector :focus-within {
  border: 0 !important;
  box-shadow: none !important;
  background-color: transparent;
}

.ant-input-outlined,
.ant-input-outlined.ant-input-status-error {
  outline: none !important;
  box-shadow: none !important;
  border: 0 !important;
  background-image: url(../src/assets/img/input_bg.png) !important;
  background-size: contain !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-color: transparent !important;
  height: 46px;
  border-radius: 0;
  padding: 0 20px;
  color: #fff;
}

.ant-form-item-label {
  padding-bottom: 5px !important;
}
.ant-form-item-label label {
  font-size: 11px !important;
}

.swap-point-wallet {
  outline: none !important;
  box-shadow: none !important;
  border: 0 !important;
  background-image: url(../src/assets/img/input-swap.png) !important;
  background-size: contain !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-color: transparent !important;
  height: 50px;
  border-radius: 0;
  color: #fff;
}

.ton-coin-wallet-swap img {
  width: 54px;
  height: 48px;
}

.swap {
